
import { NextResponse } from "next/server"
import fs from "fs/promises"
import path from "path"
import { defaultContent } from "@/lib/cms-storage"

const DATA_DIR = path.join(process.cwd(), "data")
const DATA_FILE = path.join(DATA_DIR, "cms.json")

// Ensure data directory exists
async function ensureDataDir() {
    try {
        await fs.access(DATA_DIR)
    } catch {
        await fs.mkdir(DATA_DIR, { recursive: true })
    }
}

export async function GET() {
    try {
        await ensureDataDir()
        const fileContent = await fs.readFile(DATA_FILE, "utf-8")
        const data = JSON.parse(fileContent)
        return NextResponse.json(data)
    } catch (error) {
        // If file doesn't exist or error, return default content
        console.error("Error reading CMS content:", error)
        return NextResponse.json(defaultContent)
    }
}

export async function POST(request: Request) {
    try {
        const data = await request.json()
        await ensureDataDir()
        await fs.writeFile(DATA_FILE, JSON.stringify(data, null, 2), "utf-8")
        return NextResponse.json({ success: true })
    } catch (error) {
        console.error("Error saving CMS content:", error)
        return NextResponse.json({ success: false, error: "Failed to save content" }, { status: 500 })
    }
}
